@import url(https://fonts.googleapis.com/css2?family=Caveat:wght@600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
.app{
    background: rgb(245, 245, 245);
    width: 100%;
    min-height: 100vh;
    position: relative;
    font-size: 18px;
}
.navbar{
    position: absolute;
    font-size: 4em;
    text-decoration:none;
    margin: 12px;
    z-index: 99;
}

@media screen and (max-width:500px){
    .app{
        font-size: 12px;
    }
}
.client_wrapper{
    width: 100%;
    display: grid;
    place-items: center;
    min-height: 100vh;
    padding: 2em 0;
    position: relative;
}
.client_form{
    width: 100%;
    max-width: 600px;
    position: relative;
    background: rgba(255,255,255,0.65);
    padding: 1em;
}
.input_box{
    margin-top: 60px;
    width: 100%;
    height: 100%;
    z-index: 1;
    position: relative;
}
.input_box input{
    height: 30px;
    width: 100%;
    padding: 0.5em;
    border: none;
    background: transparent;
    border-bottom: 2px solid black;
    transition: all 0.5s ease;
    font-size: 18px;
}
.input_box input:focus{
    outline: none;
    border-bottom: 2px solid turquoise;
}
.input_box span{
    position: absolute;
    pointer-events: none;
    left: 0;
    transition: -webkit-transform 0.5s ease;
    transition: transform 0.5s ease;
    transition: transform 0.5s ease, -webkit-transform 0.5s ease;
    opacity: 0;
    font-size: 22px;
}
input[placeholder]{
    font-size: 18px;
    font-weight: 200;
}
input:focus ~input[placeholder]{
    display: none;
}
.input_box input:focus ~span{
    -webkit-transform: translateY(-35px);
            transform: translateY(-35px);
    opacity: 1;
}
.client_submit{
	background: blueviolet;
	border: 4px solid #41c7c7;
	text-transform: uppercase;
	color: #fff;
	font-size: 1.3em;
	padding: 0.675em 1.35em;
	border-radius: 15px;
	transition: all 0.8s;
	position: relative;
	cursor: pointer;
	overflow: hidden;
}
.branches_container{
    width: 100%;
    padding: 0 2em;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-wrap: wrap;
    grid-gap: 50px;
    gap: 50px;
}
.branch_card{
    position: relative;
    width: 265px;
    height: 400px;
    background-color: wheat;
    padding: 10px;
    display: flex;
    flex-direction: column;
    border-radius: 10px;
    justify-content: space-evenly;
    grid-gap: 10px;
    gap: 10px;
    z-index: 1;
}
.branch_card_dp{
    display: grid;
    place-items: center;
}
.branch_card_dp img{
    border-radius: 50%;
}
.branch_card_name{
    font-size: 28px;
    display: grid;
    place-items: center;
    font-family: cursive;
}
.branch_card_name p{
    white-space: nowrap;
    text-overflow: ellipsis;
}
.branch_card_details{
    height: 50%;
}
.bad_wrapper{
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    position: relative;
    z-index: 1;
}
.branch_header{
    text-align: center;
    margin: 1em;
}
.bad_left{
    height: 250px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-transform: uppercase;
}
.bad_left h1{
    font-size: 50px;
}
.bad_right img{
    object-fit: contain;
}
.bad_left h2{
    font-size: 30px;
}
.switch_container{
    position: absolute;
    right: 0;
    top: 0;
    z-index: 8;
}
.switch_container button{
    margin-top: 20px;
    margin-right: 15px;
}
.btn_switch{
    background: rgb(243, 164, 232);
    border: 2px solid tomato;
    font-size: 18px;
    padding: 5px 10px;
    cursor: pointer;
    border-radius: 8px;
    transition: all 0.5s ease;
}
.btn_switch:hover{
    background: rgb(253, 204, 196);
    color: teal;
}
.alert_wrapper{
    width: 100%;
    display: flex;
    height: 100vh;
    position: relative;
}
.alert_wrapper_left{
    width: 30%;
    min-width: 150px;
    background-color: papayawhip;
    padding: 20px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    grid-gap: 30px;
    gap: 30px;
}
.branch_img_holder{
    display: flex;
    justify-content: center;
}
.branch_img_holder img{
    width: 100%;
    border-radius: 50%;
    border: 5px solid palevioletred;
    max-width: 400px;
}
.branch_header_holder{
    text-align: center;
    text-transform: uppercase;
}
.branch_details_holder{
    display: flex;
    flex-direction: column;
    font-size: 18px;
    grid-gap: 8px;
    gap: 8px;
}
.alert_wrapper_right{
    margin-top: 50px;
    width: 70%;
    padding: 2em;
    overflow: scroll;
}
.alerts{
    display: flex;
    flex-direction: column;
    grid-gap: 20px;
    gap: 20px;
}
.alert_wrappers{
    display: flex;
    flex-direction: column;
    grid-gap: 18px;
    gap: 18px;
}
.recent_alert{
    -webkit-animation: alert 0.5s linear;
            animation: alert 0.5s linear;
    border: 4px solid lightcoral;
} 
.alert_tab{
    width: 100%;
    background-color: khaki;
    padding: 1em;
    border-radius: 10px;
}
@-webkit-keyframes alert{
    from{
        -webkit-transform: translateX(100%);
                transform: translateX(100%);
    }
    to{
        -webkit-transform: translateX(0%);
                transform: translateX(0%);
    }
}
@keyframes alert{
    from{
        -webkit-transform: translateX(100%);
                transform: translateX(100%);
    }
    to{
        -webkit-transform: translateX(0%);
                transform: translateX(0%);
    }
}
.alert_tab_text{
    width: 100%;
    display: flex;
}
.alert_tab_cont{
    display: flex;
    justify-content: space-between;
}
.alert_count{
    position: absolute;
    top: 0;
    right: 0;
    margin: 0.5em 1.25em;
    background-color: rgb(3, 87, 165);
    border-radius: 50%;
    width: 35px;
    height: 35px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.alert_count p{
    font-size: 20px;
    font-weight: bolder;
    color: blanchedalmond;
}
.btn_load_wrapper{
    display: flex;
    justify-content: center;
}
.btn_load{
    background: rgb(83, 1, 83);
	border: 4px solid #340144;
	text-transform: uppercase;
	color: #fff;
	font-size: 18px;
	padding: 0.675em 1.35em;
	border-radius: 15px;
	transition: all 0.8s;
	position: relative;
	cursor: pointer;
	overflow: hidden;
}
.branch_form_container{
    width: 100%;
    display: flex;
    flex-direction: column;
    min-height: 100vh;
    padding: 20px;
    align-items: center;
    justify-content: center;
}
.branch_form_header{
    width: 100%;
    text-align: center;
    display: grid;
    place-items: center;
    margin: 2em;
}
.branch_form{
    width: 100%;
    max-width: 500px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
}
.branch_submit{
	background: teal;
	border: 4px solid #41c7c7;
	text-transform: uppercase;
	color: #fff;
	font-size: 1.3em;
	padding: 0.675em 1.35em;
	border-radius: 15px;
	transition: all 0.8s;
	position: relative;
	cursor: pointer;
	overflow: hidden;
}
.admin_wrapper{
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 2em;
}
.admin_wrapper h1{
    margin: 2em;
}
.admin_form{
    width: 100%;
    max-width: 500px;
}
.admin_alerts{
    display: grid;
    grid-gap: 10px;
    gap: 10px;
    padding: 20px;
}
.admin_submit{
    display: flex;
    justify-content: center;
}
.admin_header{
    position: sticky;
    top: 0;
    padding: 1em;
    background: rgba(255,255,255,0.7);
    text-align: center;
}
.welcome{
    width: 100%;
    height: 100vh;
    display: grid;
    justify-items: center;
    grid-template-rows: 1fr 4fr;
    position: relative;
    overflow: hidden;
}
.welcome_header{
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: center;
    z-index: 1;
    background-color: rgba(245,245,245,0.5);
    text-align: center;
}
.welcome_header h1{
    font-size: 3em;
    color:rgb(187, 4, 224);
    font-family: cursive;
}
.welcome_header p{
    font-size: 1.5em;
}
.link_wrapper{
    position: relative;
    margin: 5em 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    grid-gap: 3em;
    gap: 3em;
    background-color: rgba(245,245,245,0.5);
    padding: 4em;
    border-radius: 20px;
    z-index: 1;
}
.links{
    display: flex;
    align-items: center;
    text-decoration: none;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    font-size: 1.5em;
    padding: 0.5em 0.75em;
    box-shadow: 5px 5px rgba(0,0,0,0.3);
    border-radius: 10px;
    background: rgba(245,245,245,0.5);
    cursor: pointer;
}
.links p{
    margin-left: 10px;
    color: white;
}
.purple{
    background: rgb(218, 42, 253);
}
.green{
    background: rgb(61, 92, 1);
}
.blue{
    background: rgb(55, 55, 245);
}
.imgA{
    position: absolute;
    width: 280px;
    bottom: 0;
    right: 0;
    outline: 20px solid rgb(245, 174, 43);
    margin: 50px;
    -webkit-transform: rotateZ(30deg);
            transform: rotateZ(30deg);
    opacity: 0.65;
}
.imgB{
    position: absolute;
    left: 0;
    top: 35%;
    text-align: center;
    text-transform: uppercase;
    font-size: 40px;
    border: 20px solid gainsboro;
    font-family: 'Caveat', cursive;
    -webkit-transform: rotateZ(-35deg);
            transform: rotateZ(-35deg);
}
.imgB img{
    width: 500px;
    -webkit-filter: grayscale();
            filter: grayscale();
    opacity: 0.75;
}
.imgC{
  width: 200px; 
  position: absolute;
  top: 0;
  right: 20px;
  opacity: 0.75; 
}

